body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.boton-verde {
  padding: 1rem 2rem;
  text-align: center;
  text-decoration: none;
  color: #FFFFFF;
  background-color: #71B100;
  display: inline-block;
  border: none;
  margin-bottom: 2rem;
}
.boton-verde:hover {
  background-color: #619800;
}

.boton-amarillo {
  padding: 1rem 2rem;
  text-align: center;
  text-decoration: none;
  color: #FFFFFF;
  background-color: #E08709;
  display: inline-block;
  border: none;
  margin-bottom: 2rem;
}
.boton-amarillo:hover {
  background-color: #c77808;
}

.boton-amarillo-block {
  padding: 1rem 2rem;
  text-align: center;
  text-decoration: none;
  color: #FFFFFF;
  background-color: #E08709;
  display: block;
  border: none;
  margin-bottom: 2rem;
}
.boton-amarillo-block:hover {
  background-color: #c77808;
}

.boton-rojo-block {
  padding: 1rem 2rem;
  text-align: center;
  text-decoration: none;
  color: #FFFFFF;
  background-color: red;
  display: block;
  border: none;
  margin-bottom: 2rem;
  margin-top: 2rem;
}
.boton-rojo-block:hover {
  background-color: #e60000;
}

body.oscuro {
  background-color: #1a1a1a;
}
body.oscuro p, body.oscuro h1, body.oscuro h2, body.oscuro h3, body.oscuro h4, body.oscuro fieldset, body.oscuro label {
  color: #FFFFFF;
}
body.oscuro .anuncio {
  background-color: #333333;
}
body.oscuro .anuncio .anuncio__icono img {
  filter: invert(1);
}
body.oscuro .precio {
  color: #71B100;
}

.header {
  background-color: #333333;
  padding: 3rem 0;
}
.header.inicio {
  background-image: url("../img/header.jpg");
  min-height: 70rem;
  background-size: cover;
  background-position: center;
}
.header.inicio .contenido__header {
  min-height: 70rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.header.inicio .contenido__header h1 {
  color: #FFFFFF;
  text-align: left;
  font-weight: bold;
  max-width: 60rem;
}

.barra {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.barra .nav {
  transition: opacity 0.8s ease-in;
}
.barra .nav a {
  text-decoration: none;
  color: #FFFFFF;
  font-size: 1.8rem;
  margin-right: 1rem;
}
.barra .nav a:last-of-type {
  margin-right: 0;
}

.barra__burguer {
  width: 4rem;
  display: none;
}

.nav__derecha {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.dark-mode {
  width: 3rem;
  filter: invert(1);
}

@media (max-width: 768px) {
  .barra {
    flex-direction: column;
  }
  .barra .barra__burguer {
    display: block;
  }
  .barra .nav__derecha {
    align-items: center;
  }
  .barra .nav__derecha .dark-mode {
    margin-top: 2rem;
  }
  .barra .nav {
    margin-top: 1rem;
    visibility: hidden;
    opacity: 0;
    height: 0;
  }
  .barra .nav.ocultar {
    height: auto;
    visibility: visible;
    opacity: 1;
    transition: visibility 0.5s ease-in-out, opacity 0.5s ease-in;
  }
  .barra .nav a {
    display: block;
    text-align: center;
    margin-right: 0;
  }
}
.footer {
  background-color: #333333;
  padding: 2rem 0;
}
.footer .nav {
  padding: 4rem 0 0 0;
}
.footer .nav a {
  text-decoration: none;
  color: #FFFFFF;
  font-size: 1.8rem;
  margin-right: 1rem;
}
.footer .nav a:last-of-type {
  margin-right: 0;
}
.footer .copyrigth {
  text-align: center;
  color: #FFFFFF;
}

@media (max-width: 768px) {
  .footer .nav {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 0;
  }
  .footer .nav a {
    display: block;
    font-size: 2rem;
    margin-right: 0;
  }
}
.iconos__contenedor {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(3, 1fr);
}
.iconos__contenedor .icono {
  text-align: center;
}
.iconos__contenedor .icono img {
  height: 10rem;
}
.iconos__contenedor .icono h3 {
  text-transform: uppercase;
}

.anuncios {
  padding: 2rem 0;
}

.contenedor__anuncios {
  padding-bottom: 2rem;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(3, 1fr);
}
.contenedor__anuncios .anuncio {
  border: 1px solid #e1e1e1;
  background-color: #e1e1e1;
}
.contenedor__anuncios .anuncio h3, .contenedor__anuncios .anuncio p {
  margin: 0;
  max-width: 20rem;
}

.anuncio__contenedor {
  padding: 1rem 2rem;
}

.precio {
  color: #71B100;
  font-size: 3rem;
  font-weight: 700;
}

.anuncio__iconos {
  display: flex;
  list-style: none;
  padding: 0;
  max-width: 40rem;
}
.anuncio__iconos .anuncio__icono {
  margin-right: 2rem;
  flex: 1;
  display: flex;
  align-items: center;
}
.anuncio__iconos .anuncio__icono img {
  height: 5rem;
}

.anuncio-casa {
  padding-bottom: 2rem;
}

@media (max-width: 768px) {
  .contenedor__anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(1, 1fr);
  }
}
.contacto-imagen {
  background-image: url("../img/encuentra.jpg");
  background-position: center;
  background-size: cover;
  text-align: center;
  padding: 10rem 0 8rem 0;
}
.contacto-imagen h2 {
  color: #FFFFFF;
  font-weight: bold;
  font-size: 4rem;
}
.contacto-imagen p {
  color: #FFFFFF;
  padding: 4rem 0 2rem 0;
}

.testimoniales {
  display: grid;
  grid-template-columns: 2fr 1fr;
  column-gap: 2rem;
  padding-bottom: 2rem;
}

.blog {
  padding-bottom: 2rem;
}

.entrada-blog {
  display: grid;
  grid-template-columns: 1fr 2fr;
  column-gap: 2rem;
  margin-bottom: 2rem;
}
.entrada-blog:last-of-type {
  margin-bottom: 0;
}

.texto-meta span {
  color: #E08709;
}

.entrada-texto a {
  text-decoration: none;
}
.entrada-texto h4 {
  color: #000000;
  margin: 0;
}
.entrada-texto h4::after {
  content: "";
  display: block;
  width: 10rem;
  height: 0.5rem;
  background-color: #71B100;
}

.testimonio-contenedor {
  background-color: #71B100;
  padding: 2rem;
  border-radius: 2rem;
  color: #FFFFFF;
  font-size: 2.4rem;
}
.testimonio-contenedor blockquote {
  position: relative;
  padding-left: 5rem;
}
.testimonio-contenedor blockquote::before {
  content: "";
  display: block;
  height: 5rem;
  width: 6rem;
  background-image: url("../img/comilla.svg");
  position: absolute;
  left: -2rem;
}
.testimonio-contenedor p {
  color: #FFFFFF;
  text-align: right;
}

@media (max-width: 768px) {
  .testimoniales {
    grid-template-columns: 1fr;
  }

  .entrada-blog {
    grid-template-columns: 1fr;
  }
}
.formulario fieldset {
  margin-bottom: 2rem;
}
.formulario legend {
  font-size: 2rem;
}
.formulario label {
  display: block;
}
.formulario input, .formulario textarea, .formulario select {
  border-radius: 2rem;
  border: 1px solid #e1e1e1;
  padding: 1rem 2rem;
  width: 100%;
  outline: none;
}
.formulario textarea {
  height: 15rem;
}
.formulario p {
  font-size: 1.4rem;
  color: #e1e1e1;
}

.radios {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 30rem;
}
.radios input[type=radio] {
  width: auto;
}

.nosotros__contenedor {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(2, 1fr);
}
.nosotros__contenedor h4 {
  font-size: 2.5rem;
  margin: 1.5rem 0;
}

@media (max-width: 768px) {
  .nosotros__contenedor {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(1, 1fr);
  }
}
.error {
  background-color: #e60000;
  padding: 1rem 0;
  color: #FFFFFF;
  font-weight: bold;
  text-transform: uppercase;
  margin-bottom: 2rem;
  padding: 0 2rem;
}

.exito {
  background-color: #71B100;
  padding: 1rem 0;
  color: #FFFFFF;
  font-weight: bold;
  text-transform: uppercase;
}

table.propiedades {
  width: 100%;
  text-align: center;
  border-spacing: 0;
}
table.propiedades thead {
  color: #FFFFFF;
  background-color: #71B100;
}
table.propiedades thead th {
  padding: 2rem 0;
}
table.propiedades tbody .imagen-tabla {
  width: 10rem;
  display: inline-block;
}

.imagen-corta {
  width: 20rem;
}

.w-100 {
  width: 100%;
}